import React, { useEffect, useRef, useState } from 'react';
import { Container, Grid, Box, Typography } from '@mui/material';
import { useTheme } from '@mui/material/styles';
import HeroSection from '../components/HeroSection/HeroSection';
import InfoCard from '../components/InfoCard';

const Home = ({ isMobile }) => {
  const theme = useTheme();
  const infoCardRef = useRef(null);

  const descriptions = {
    simple: "Gyorsan s egyszeren generlhat rarendet nhny kattintssal. Ez az alkalmazs segt a dikoknak az rarendjk megtervezsben, minimalizlva az tkzseket s a szabadid-kiesseket. Az alkalmazs rtestseket s emlkeztetket kld a kzelg rkrl s fontos hatridkrl. Clunk, hogy megknnytsk a hallgatk lett s nveljk a tanulmnyi hatkonysgot.",
    advanced: "Halad belltsokkal testreszabhatja az rarend generlst. Ez az alkalmazs segt a dikoknak az rarendjk megtervezsben, minimalizlva az tkzseket s a szabadid-kiesseket. Az alkalmazs rtestseket s emlkeztetket kld a kzelg rarendekrl s fontos hatridkrl. Clunk, hogy megknnytsk a hallgatk lett s nveljk a tanulmnyi hatkonysgot.",
    custom: "Teljesen egyedi rarendet hozhat ltre sajt ignyei szerint. Ez az alkalmazs segt a dikoknak az rarendjk megtervezsben, minimalizlva az tkzseket s a szabadid-kiesseket. Az alkalmazs rtestseket s emlkeztetket kld a kzelg rarendekrl s fontos hatridkrl. Clunk, hogy megknnytsk a hallgatk lett s nveljk a tanulmnyi hatkonysgot."
  };

  return (
    <Container sx={{ mt: 15 }}>
      <Box sx={{ display: 'flex', justifyContent: 'center', alignItems: 'center', minHeight: '50vh' }}>
        <HeroSection />
      </Box>
      <Box sx={{ mt: 20 }} /> {/* Add more padding here */}
      <Typography variant="h4" component="h2" sx={{ textAlign: 'center', mb: 4 }}>
        Funkcik
      </Typography>
      <Grid container spacing={4} sx={{ mt: 4 }} ref={infoCardRef}>
        <Grid item xs={12} sm={4}>
          <InfoCard
            title="Gyors Generls"
            description={descriptions.simple}
            delay={0}
            theme={theme}
            sx={{ height: '100%' }} // Ensure equal height
          />
        </Grid>
        <Grid item xs={12} sm={4}>
          <InfoCard
            title="Halad Generls"
            description={descriptions.advanced}
            delay={300}
            theme={theme}
            sx={{ height: '100%' }} // Ensure equal height
          />
        </Grid>
        <Grid item xs={12} sm={4}>
          <InfoCard
            title="Egyedi Generls"
            description={descriptions.custom}
            delay={600}
            theme={theme}
            sx={{ height: '100%' }} // Ensure equal height
          />
        </Grid>
      </Grid>
    </Container>
  );
};

export default Home;
