import React, { useState } from 'react';
import { Box, Typography, Button, Collapse } from '@mui/material';
import { useTheme } from '@mui/material/styles';

const HeroSection = () => {
  const [expanded, setExpanded] = useState(false);
  const theme = useTheme();

  const handleToggle = () => {
    setExpanded(!expanded);
  };

  return (
    <Box
      sx={{
        backgroundColor: theme.palette.background.default,
        outline: `3px solid ${theme.palette.primary.main}`,
        color: theme.palette.text.primary,
        p: 4,
        borderRadius: 2,
        boxShadow: 3,
        overflow: 'hidden',
        transition: 'all 0.3s ease-in-out',
        [theme.breakpoints.down('sm')]: {
          p: 2,
        },
      }}
    >
      <Box
        sx={{
          display: 'flex',
          flexDirection: { xs: 'column', md: 'row' },
          alignItems: 'center',
          mb: 2,
        }}
      >
        <Box
          component="img"
          src={`${process.env.PUBLIC_URL}/logo.png`}
          alt="Hephaistos Logo"
          sx={{
            width: { xs: 100, md: 150 },
            height: { xs: 100, md: 150 },
            mr: { md: 2 },
            mb: { xs: 2, md: 0 },
          }}
        />
        <Typography variant="h3" component="h1">
          Hephaistos
        </Typography>
      </Box>
      <Typography variant="body1" sx={{ mt: 2 }}>
        A kzpiskolai projektnk clja egy olyan egyetemi rarendgenerl alkalmazs
        kifejlesztse, amely segti a dikokat az rarendjk megtervezsben,
        minimalizlva az tkzseket s a szabadid-kiesseket. Az alkalmazsunk abban nyjt segtsget, hogy optimalizlt rarendet ksztsen, figyelembe vve a kvetelmnyeket s az elfeltteleket.
      </Typography>

      {/* Collapse komponens az animcihoz */}
      <Collapse in={expanded}>
        <Typography variant="body1" sx={{ mt: 2 }}>
          Ez az alkalmazs nemcsak azoknak a dikoknak nyjt megoldst, akik kssben vannak
          a tanulmnyaikkal, hanem azoknak is, akik elre szeretnk ltni a lehetsges
          lett, cskkenti a stresszt s nveli a tanulmnyi hatkonysgot. Az alkalmazs
          rtestseket s emlkeztetket kld a felhasznlknak a kzelg rikrl s fontos
          hatridkrl.
        </Typography>
      </Collapse>

      <Box sx={{ display: 'flex', justifyContent: 'center', mt: 2 }}>
        <Button variant="contained" onClick={handleToggle}>
          {expanded ? 'Bezrs' : 'Rlunk'}
        </Button>
      </Box>
    </Box>
  );
};

export default HeroSection;
